import lodash from 'lodash';

export interface CounterPlayerProps {
  id: string;
  label?: string;
  maxCount?: number;
}

export interface CounterPlayerState {
  id: string;
  label: string;
  count: number;
  maxCount: number;
  atMax: boolean;
}

export class CounterPlayer {
  private count: number;
  private readonly id: string;
  private label: string;
  private readonly maxCount: number;

  constructor(props: CounterPlayerProps) {
    this.id = props.id;
    this.label = props.label ?? 'Player';
    this.maxCount = props.maxCount ?? 10;
    this.count = 0;
  }

  getState(): CounterPlayerState {
    const limit = Math.abs(this.maxCount);
    const atMax = Math.abs(this.count) >= limit;

    return {
      id: this.id,
      label: this.label,
      count: this.count,
      maxCount: this.maxCount,
      atMax
    };
  }

  increment(step = 1): CounterPlayerState {
    const normalizedStep = lodash.isFinite(step) ? Math.max(1, step) : 1;
    this.count = this.clamp(this.count + normalizedStep);
    return this.getState();
  }

  decrement(step = 1): CounterPlayerState {
    const normalizedStep = lodash.isFinite(step) ? Math.max(1, step) : 1;
    this.count = this.clamp(this.count - normalizedStep);
    return this.getState();
  }

  reset(): CounterPlayerState {
    this.count = 0;
    return this.getState();
  }

  rename(label: string): CounterPlayerState {
    this.label = lodash.trim(label) || this.label;
    return this.getState();
  }

  private clamp(value: number): number {
    const limit = Math.abs(this.maxCount);

    return Math.min(Math.max(-limit, value), limit);
  }
}
