import { css, html, LitElement } from 'lit';
import type { PropertyValues } from 'lit';
import { property, state } from 'lit/decorators.js';

import { CounterPlayer } from '../player.js';

export const COUNTER_PLAYER_TAG = `counter-player-v${process.env.PLAYER_VERSION ?? '1'}`;

export class CounterPlayerElement extends LitElement {
  @property({ type: String, attribute: 'player-id' })
  playerId = 'demo';

  @property({ type: String })
  label = 'Demo Player';

  @property({ type: Number, attribute: 'max-count' })
  maxCount = 5;

  @state()
  private playerState = new CounterPlayer({
    id: this.playerId,
    label: this.label,
    maxCount: this.maxCount
  }).getState();

  private player = new CounterPlayer({
    id: this.playerId,
    label: this.label,
    maxCount: this.maxCount
  });

  protected willUpdate(changed: PropertyValues<this>): void {
    if (
      changed.has('playerId') ||
      changed.has('label') ||
      changed.has('maxCount')
    ) {
      this.player = new CounterPlayer({
        id: this.playerId,
        label: this.label,
        maxCount: this.maxCount
      });
      this.syncState();
    }
  }

  private syncState(): void {
    this.playerState = this.player.getState();
  }

  private onIncrement(): void {
    this.player.increment();
    this.syncState();
  }

  private onDecrement(): void {
    this.player.decrement();
    this.syncState();
  }

  private onReset(): void {
    this.player.reset();
    this.syncState();
  }

  render() {
    const statusText = this.playerState.atMax
      ? 'Reached the Limit.'
      : 'Ready to play.';

    return html`
      <section class="test-player-widget">
        <header class="test-player-widget__header">
          <h2 class="test-player-widget__title">${this.playerState.label}</h2>
          <p class="test-player-widget__meta">ID: ${this.playerState.id}</p>
        </header>

        <div class="test-player-widget__stats">
          <span class="test-player-widget__count"
            >Count: ${this.playerState.count}</span
          >
          <span class="test-player-widget__limit"
            >Max: ${this.playerState.maxCount}</span
          >
        </div>

        <p
          class="test-player-widget__status"
          data-at-max=${String(this.playerState.atMax)}
        >
          ${statusText}
        </p>

        <div class="test-player-widget__actions">
          <button type="button" @click=${this.onDecrement}>-</button>
          <button type="button" @click=${this.onIncrement}>+</button>
          <button type="button" @click=${this.onReset}>Reset</button>
        </div>
      </section>
    `;
  }

  static styles = css`
    :host {
      display: block;
    }

    .test-player-widget {
      border: 1px solid #d0d7de;
      border-radius: 12px;
      padding: 16px;
      display: grid;
      gap: 12px;
      max-width: 320px;
      font-family:
        ui-sans-serif,
        system-ui,
        -apple-system,
        'Segoe UI',
        sans-serif;
    }

    .test-player-widget__header {
      display: flex;
      justify-content: space-between;
      align-items: baseline;
      gap: 12px;
    }

    .test-player-widget__title {
      font-size: 18px;
      font-weight: 600;
      margin: 0;
    }

    .test-player-widget__meta {
      font-size: 12px;
      color: #57606a;
      margin: 0;
    }

    .test-player-widget__stats {
      display: flex;
      gap: 12px;
      font-size: 14px;
    }

    .test-player-widget__status {
      font-size: 14px;
      margin: 0;
    }

    .test-player-widget__status[data-at-max='true'] {
      color: #b42318;
      font-weight: 600;
    }

    .test-player-widget__actions {
      display: flex;
      gap: 8px;
    }

    .test-player-widget__actions button {
      border: 1px solid #cfd7de;
      background: #ffffff;
      border-radius: 6px;
      padding: 6px 10px;
      cursor: pointer;
    }
  `;
}

export const defineCounterPlayer = (): void => {
  if (!customElements.get(COUNTER_PLAYER_TAG)) {
    customElements.define(COUNTER_PLAYER_TAG, CounterPlayerElement);
  }
};
